import React from "react";
import {Formik, Form} from "formik"; //     
import * as Yup from 'yup' //       Yup
import classes from "./MyPosts.module.css"
//import DisplayFormikState from "../../../../common/formikCommon/DisplayFormikState"
import {MyTextInput} from "../../../../common/formikCommon/MyFieldsBS"
import Button from "react-bootstrap/Button";
import Stack from "react-bootstrap/Stack";

let myInitialValues = { //   
    newPost: "",
}
let myValidationSchema = Yup.object({ //    required,    
})

type MyPostsFormikType = {
    addPost: (newPostData: string) => void

}
type ValuesType = {
    newPost: string
}
const MyPostsFormik:React.FC<MyPostsFormikType> = ({addPost}) => { //     ,     
    const myOnSubmit = (values:ValuesType, {resetForm}:any) => { //   
        addPost(values.newPost) // ,     
        resetForm()//     
    }

    return (
        <>
            <Formik
                initialValues={myInitialValues}
                validationSchema={myValidationSchema}
                onSubmit={myOnSubmit}
            >

                {({
                      handleReset,//  
                  }) => ( //           (  )
                    <Form className={classes.MyPosts}>
                        <div>
                            <MyTextInput //   MyPostsBS
                                label=""
                                autoFocus={false}
                                name='newPost'
                                type='textarea'
                                placeholder='  '
                                leftLabelLength={""}
                            />

                            {/*     */}
                            <Stack direction="vertical" gap={1} className={'mt-2'}>
                                <Button type="submit"> {/*  */}
                                    Submit
                                </Button>
                                <Button
                                    variant="warning"
                                    type='button'
                                    onClick={handleReset}
                                >Reset
                                </Button>
                            </Stack>

                        </div>
                        <div/>
                        {/*  <DisplayFormikState/> {/*   */}
                    </Form>

                )}

            </Formik>
        </>
    )
}

export default MyPostsFormik
