import React from "react";
import {Formik, Form} from "formik"; //     
import * as Yup from 'yup' //       Yup
import classes from './EditProfile.module.css'
//import DisplayFormikState from "../../../common/formikCommon/DisplayFormikState"
import {MyCheckbox, MyTextInput} from "../../../common/formikCommon/MyFieldsBS"
import Button from "react-bootstrap/Button";
import {ProfileType} from "../../../../types/commonTypes";
import {getProfileType} from "../../../api/apiTypes";

let myValidationSchema = Yup.object({ //    required,    
})

type EditProfileFormikType = {
    putProfile: ProfileType,
    setEditMode: (editMode: boolean)=> void
    profile: getProfileType,
    editProfileStatus:Array<string>,
    setEditProfileStatus: (editProfileStatus: Array<string>)=> void

}
const EditProfileFormik:React.FC<EditProfileFormikType> = (
    {putProfile, setEditMode, profile, editProfileStatus, setEditProfileStatus}
    ) => { //     ,     

    const myInitialValues = { //   
        FullName: profile.fullName,
        AboutMe: profile.aboutMe,
        LookingForAJob: profile.lookingForAJob,
        LookingForAJobDescription: profile.lookingForAJobDescription,
        contacts: profile.contacts, //     
    }

    const myOnSubmit = (values:ProfileType, {resetForm}:any) => { //   
        // @ts-ignore
        putProfile(values) // ,     
        resetForm()//     

    }

    return (
        React.createElement(Formik, {
            initialValues: myInitialValues, //   
            validationSchema: myValidationSchema, //  
            // @ts-ignore
            onSubmit: myOnSubmit //   
        }, ({
                // @ts-ignore
                handleReset,//  
            }) => ( //           (  )
        React.createElement(Form, {className: classes.MyPosts},

            React.createElement('div', {},
                React.createElement('div', // 
                        {className: classes.HeaderEditProfileForm},
                        ' '
                    ),
                React.createElement(MyTextInput, // :
                        {
                            label: "",
                            autoFocus: false,
                            name: 'FullName',
                            type: 'text',
                            placeholder: '',
                            leftLabelLength: "5rem" //   input  label  
                        }
                    ),

                React.createElement(MyTextInput,// 
                        {label: " ", autoFocus: false, name: 'AboutMe', type: 'text', placeholder: ' ', leftLabelLength: "5rem"}, //   input  label  
                    ),

                // @ts-ignore
                React.createElement(MyCheckbox, {name: 'LookingForAJob'}, //  
                        "  "
                    ),

                React.createElement(MyTextInput, { //  
                            label: "", autoFocus: false, name: 'LookingForAJobDescription',
                            type: 'textarea', placeholder: '', leftLabelLength: "5rem" //   input  label  
                        }
                    ),

                React.createElement('h4', {},//     
                        ":"
                    ),

                React.createElement('div', {className: classes.EditProfileContactsFields},
                    Object.keys(profile.contacts).map((c) => { //   , 
                            return React.createElement('div', {key: c},
                                React.createElement(
                                    MyTextInput,
                                    {
                                        label: c,
                                        autoFocus: false,
                                        name: 'contacts[' + c + ']',
                                        type: 'text',
                                        placeholder: c,
                                        leftLabelLength: "5rem" //   input  label  
                                    },
                                ),
                                React.createElement('div', {}, //    
                                    editProfileStatus.map(err => {//         
                                        if (err.toLowerCase().includes(c.toLowerCase())) { //     ""     
                                            return React.createElement('div', {key: err, className: classes.errorText}, err )//       
                                        }
                                        return null
                                    })
                                ),
                            )
                        })
                    ),
                React.createElement('br'), // 

                    //     
                React.createElement(Button, {
                        type: 'button',
                        variant: "warning",
                        onClick: () => { //     
                            handleReset();//     
                            setEditProfileStatus([]); //      
                        }
                    }, ''),

                    " ", //  

                    //  
                React.createElement(Button, {type: 'submit'}, ''),

                    " ", //  

                    //
                React.createElement(Button, {
                        variant: "secondary",
                        onClick: () => { //     
                            setEditMode(false)//       
                            setEditProfileStatus([]) //      
                        }
                    }, ''),
                )
            )
        ))
    )
}

export default EditProfileFormik

